import * as React from 'react';
import { StyledEdgesPath } from './GraphStyles';
import { EdgeType } from '../PureLinkGraphUtils';

export interface EdgesProps {
    edges: EdgeType[];
    linkWidth: number;
}

const Edges = (props: EdgesProps): React.ReactElement => {
    const { edges, linkWidth } = props;
    let key = '';
    return (
        <svg
            style={{
                position: 'absolute',
                overflow: 'visible',
                pointerEvents: 'none',
            }}
            height="100%"
            width="100%"
            data-test="edges"
        >
            {edges.map(edge => {
                key = `edge-${edge.sourceNode}-${edge.targetNode}`;
                return [<StyledEdgesPath className="edge" key={key} data-test={key} linkWidth={linkWidth} />];
            })}
        </svg>
    );
};

export default Edges;
