import { each, filter, sortBy } from 'lodash';

export interface onNodeClickType {
    payload: {
        field: string;
        value: string;
        state: string;
    };
}

export type EdgeType = {
    sourceNode: string;
    targetNode: string;
};

export type NodeDataType = {
    field: string;
    fieldIndex: number;
    value: string;
    ins: Record<string, EdgeType>;
    outs: Record<string, EdgeType>;
    count: number;
};

export type NodeType = {
    x: number;
    y: number;
    w: number;
    h: number;
    currentX: number;
    currentY: number;
    currentW: number;
    currentH: number;
    time: number;
    data: NodeDataType;
};

export type FieldType = {
    count: number;
};

export type GraphType = {
    nodes: Record<string, NodeType>;
    edges: EdgeType[];
    fields: Record<string, FieldType>;
};

export const cleanString = (s: string) => String(s).replace(/[^a-zA-Z0-9\\-]/gi, '');

export const OFFSET_Y = 35;

/**
 *
 * @param fields
 * @param columns
 * @param nodeWidth
 * @param nodeHeight
 * @param nodeSpacingX
 * @param nodeSpacingY
 * @param resultLimit
 * @param fieldOrder
 * @returns GraphType
 * Translates fields and column data into GraphType object.
 * Node placement is figured out here - see NodeType
 * We transverse over fields
 * Then for every field we go down the column
 * We create a node
 * look to the right one column, create an edge
 * look to the left one column, create an edge
 */
export const createLinkGraphFromTable = (
    fieldsIn: string[],
    columnsIn: any[],
    nodeWidth: number,
    nodeHeight: number,
    nodeSpacingX: number,
    nodeSpacingY: number,
    resultLimit: number,
    fieldOrder: string[]
) => {
    const nodes = {};
    const graphDataModel: GraphType = {
        nodes: {},
        edges: [],
        fields: {},
    };
    if (!fieldsIn || !Array.isArray(fieldsIn) || !columnsIn || !Array.isArray(columnsIn)) {
        return graphDataModel;
    }
    let fields = fieldsIn;
    let columns = columnsIn;
    if (fieldOrder.length) {
        fields = fieldOrder;
        columns = [];
        fields.forEach((field, fieldIndex) => {
            columns[fieldIndex] = columnsIn[fieldsIn.indexOf(field)];
        });
    }
    fields.forEach((field: string, fieldIndex: number) => {
        nodes[field] = {
            nodes: {},
        };
        graphDataModel.fields[field] = {
            count: 0,
        };
        if (!columns[fieldIndex]) {
            return graphDataModel;
        }
        columns[fieldIndex].find((column: string, columnIndex: number) => {
            const key = `${fieldIndex}-${cleanString(field)}-${cleanString(column)}`;
            let node = nodes[field].nodes[column];
            if (!nodes[field].nodes[column]) {
                node = {
                    data: {
                        field,
                        fieldIndex,
                        count: 1,
                        value: column,
                        ins: {},
                        outs: {},
                    },
                };
            } else {
                node.data.count += 1;
            }
            if (columns[fieldIndex + 1] && columns[fieldIndex + 1][columnIndex]) {
                const sourceNode = `${fieldIndex}-${cleanString(field)}-${cleanString(column)}`;
                const targetNode = `${fieldIndex + 1}-${cleanString(fields[fieldIndex + 1])}-${cleanString(
                    columns[fieldIndex + 1][columnIndex]
                )}`;
                const edge = {
                    sourceNode,
                    targetNode,
                };
                node.data.outs[
                    `${cleanString(column)}-${cleanString(columns[fieldIndex + 1][columnIndex])}`
                ] = edge;
                graphDataModel.edges.push(edge);
            }
            if (columns[fieldIndex - 1] && columns[fieldIndex - 1][columnIndex]) {
                node.data.ins[`${column}-${columns[fieldIndex - 1][columnIndex]}`] = {
                    out: columns[fieldIndex - 1][columnIndex],
                    in: column,
                };
            }
            if (!graphDataModel.nodes[key]) {
                nodes[field].nodes[column] = node;
                graphDataModel.nodes[key] = node;
                node.x = fieldIndex * nodeWidth + fieldIndex * nodeSpacingX;
                node.y =
                    (Object.keys(nodes[field].nodes).length - 1) * nodeHeight +
                    (Object.keys(nodes[field].nodes).length - 1) * nodeSpacingY +
                    OFFSET_Y;
                node.w = nodeWidth;
                node.h = nodeHeight;
            }
            graphDataModel.fields[field].count = Object.keys(nodes[field].nodes).length;
            return Object.keys(nodes[field].nodes).length === resultLimit;
        });
    });
    return graphDataModel;
};

/**
 * Get all paths from a node. If sourceNodeID is passed, all the paths would
 * start with this id. Return value is an object where id of start node is the key,
 * array of path is the value. Given a graph like:
 * '7' - '8'- '9' - '1' - '2' - '4' - '5' - '6'
              '0' -         - '3'
 *
 * getAllPathsFromNodeID(json, '1') will return:
 *  {
 *      1: [
 *          [1, 2, 4, 5, 6],
 *          [1, 2, 3]
 *      ]
 *  }
 *
 * @param {Object} graph
 * @param {String} sourceNodeID Node ID to start from
 * @return {Object}
 */
export const getAllPathsFromNodeID = (graph: GraphType, sourceNodeID: string, cache = {}) => {
    if (!graph || !sourceNodeID) {
        return { nodes: {}, edges: [], fields: {} };
    }
    const sourceNodeIDs = [sourceNodeID];
    const allPaths = {};
    each(sourceNodeIDs, id => {
        const currentPathArray = [];
        each(filter(graph.edges, { sourceNode: id }), ({ targetNode }) => {
            if (!cache[targetNode]) {
                getAllPathsFromNodeID(graph, targetNode, cache);
            }
            const paths = cache[targetNode];
            each(paths, path => {
                const pathCopy = path.slice();
                pathCopy.unshift(id);
                currentPathArray.push(pathCopy);
            });
        });
        if (!currentPathArray.length) {
            currentPathArray.push([id]);
        }
        // eslint-disable-next-line no-param-reassign
        cache[id] = currentPathArray;
        allPaths[id] = currentPathArray;
    });
    return allPaths;
};

/**
 * Get all paths from a graph. All the paths would
 * end with this id. Return value is an object where id of end node is the key,
 * array of path is the value. Given a graph like:
 * '7' - '8'- '9' - '1' - '2' - '4' - '5' - '6'
              '0' -         - '3'
 *
 * getAllPathsToNodeID(json, '2') will return:
 *  {
 *      2: [
 *          [7, 8, 9, 1, 2],
 *          [0, 1, 2]
 *      ]
 *  }
 *
 * @param {Graph} graph
 * @param {String} targetNodeID Node ID to end with
 * @return {Object}
 */
export const getAllPathsToNodeID = (graph: GraphType, targetNodeID: string, cache = {}) => {
    if (!graph || !targetNodeID) {
        return {};
    }
    const targetNodeIDs = [targetNodeID];
    const allPaths = {};
    each(targetNodeIDs, (id: string) => {
        const currentPathArray = [];
        each(filter(graph.edges, { targetNode: id }), ({ sourceNode }) => {
            if (!cache[sourceNode]) {
                getAllPathsToNodeID(graph, sourceNode, cache);
            }
            const paths = cache[sourceNode];
            each(paths, path => {
                const pathCopy: string[] = path.slice();
                pathCopy.push(id);
                currentPathArray.push(pathCopy);
            });
        });
        if (!currentPathArray.length) {
            currentPathArray.push([id]);
        }
        const currentCache = cache;
        currentCache[id] = currentPathArray;
        allPaths[id] = currentPathArray;
    });
    return allPaths;
};

// TODO: write tests once all sorting options are supported
export const sortGraphColumn = (
    graph: GraphType,
    field: string,
    sortKey: string,
    nodeHeight: number,
    nodeSpacingY: number
) => {
    const nodesToSort: NodeType[] = Object.values(graph.nodes).filter(node => node.data.field === field);
    const nodes: NodeType[] = sortBy(nodesToSort, node => node.data.value);
    if (sortKey === 'Z-A') {
        nodes.reverse();
    }
    nodes.forEach((node, index) => {
        const currentNode = node;
        currentNode.y = index * nodeHeight + index * nodeSpacingY + OFFSET_Y;
    });
};
