import * as React from 'react';
import { cloneDeep } from 'lodash';
import Edges from './Edges';
import Nodes from './Nodes';
import { StyledViewport } from './GraphStyles';
import {
    GraphType,
    EdgeType,
    getAllPathsFromNodeID,
    getAllPathsToNodeID,
    cleanString,
    NodeType,
    onNodeClickType,
} from '../PureLinkGraphUtils';

type GraphState = {
    nodes: Record<string, NodeType>;
    edges: [];
    highlightedNodes: string[];
    hasMounted: boolean;
};

type GraphProps = {
    graphDataModel: GraphType;
    onNodeClick: (arg0: onNodeClickType) => void;
    nodeColor: string;
    nodeHighlightColor: string;
    linkColor: string;
    linkWidth: number;
    showValueCounts: boolean;
};

class Graph extends React.Component<GraphProps, GraphState> {
    requestAnimationFrameId: number;

    viewport: HTMLElement;

    constructor(props) {
        super(props);
        this.requestAnimationFrameId = null;
        this.viewport = null;
        this.state = {
            nodes: {},
            edges: [],
            highlightedNodes: [],
            hasMounted: false,
        };
    }

    static getDerivedStateFromProps(props: GraphProps, state: GraphState) {
        if (!state.hasMounted) {
            return state;
        }
        const nodes = cloneDeep(props.graphDataModel.nodes);
        Object.keys(nodes).forEach(key => {
            const nodeFromProps = nodes[key];
            const nodeFromState = state.nodes[key];
            if (!nodeFromState) {
                nodeFromProps.time = 1;
            } else if (
                nodeFromProps.x !== nodeFromState.x ||
                nodeFromProps.y !== nodeFromState.y ||
                nodeFromProps.w !== nodeFromState.w ||
                nodeFromProps.h !== nodeFromState.h
            ) {
                nodeFromProps.time = 0;
            }
            nodeFromProps.currentX = nodeFromProps.x;
            nodeFromProps.currentY = nodeFromProps.y;
            nodeFromProps.currentW = nodeFromProps.w;
            nodeFromProps.currentH = nodeFromProps.h;
        });

        let highlightedNodes = [...state.highlightedNodes];

        state.highlightedNodes.forEach(nodeID => {
            if (!props.graphDataModel.nodes[cleanString(nodeID)]) {
                highlightedNodes = [];
            }
        });

        return {
            nodes,
            edges: props.graphDataModel.edges,
            highlightedNodes,
        };
    }

    componentDidMount() {
        this.setState({
            hasMounted: true,
        });
    }

    componentDidUpdate() {
        const { highlightedNodes } = this.state;
        cancelAnimationFrame(this.requestAnimationFrameId);
        this.highlightAllEdges();
        this.unhighlightAllNodes();
        highlightedNodes.forEach(value => {
            this.handleMouseEnterNode(cleanString(value));
        });
        this.tween();
    }

    componentWillUnmount() {
        cancelAnimationFrame(this.requestAnimationFrameId);
    }

    handleMouseEnterNode(nodeID: string) {
        this.unhighlightAllEdges();
        const allPathsFromNodeID = getAllPathsFromNodeID(this.props.graphDataModel, nodeID);
        const allPathsToNodeID = getAllPathsToNodeID(this.props.graphDataModel, nodeID);
        let lastUUID;
        Object.keys(allPathsFromNodeID).forEach(pathKey => {
            allPathsFromNodeID[pathKey].forEach((path: []) => {
                path.forEach(uuid => {
                    let element = this.viewport.querySelector(`[data-test="node-${uuid}"]`);
                    if (element) {
                        element.classList.add('hovering');
                    }
                    element = this.viewport.querySelector(`[data-test="edge-${lastUUID}-${uuid}"]`);
                    if (element) {
                        element.classList.add('hovering');
                    }
                    lastUUID = uuid;
                });
            });
        });
        lastUUID = null;
        Object.keys(allPathsToNodeID).forEach(pathKey => {
            allPathsToNodeID[pathKey].forEach(path => {
                path.forEach((uuid: string) => {
                    let element = this.viewport.querySelector(`[data-test="node-${uuid}"]`);
                    if (element) {
                        element.classList.add('hovering');
                    }
                    element = this.viewport.querySelector(`[data-test="edge-${lastUUID}-${uuid}"]`);
                    if (element) {
                        element.classList.add('hovering');
                    }
                    lastUUID = uuid;
                });
            });
        });
    }

    handleMouseLeave() {
        const { highlightedNodes } = this.state;
        [...this.viewport.getElementsByClassName('hovering')[Symbol.iterator]()].forEach(ele => {
            ele.classList.remove('hovering');
        });
        highlightedNodes.forEach(value => {
            this.handleMouseEnterNode(cleanString(value));
        });
        this.highlightAllEdges();
    }

    handleMouseClick(fieldIndex: number, field: string, value: string) {
        const { onNodeClick } = this.props;
        const { highlightedNodes } = this.state;
        const key = `${fieldIndex}-${field}-${value}`;
        const newHighlightedNodes: string[] = [...highlightedNodes];
        const index = newHighlightedNodes.indexOf(key);
        const element = this.viewport.querySelector(
            `[data-test="node-${fieldIndex}-${cleanString(field)}-${cleanString(value)}"]`
        );
        let state = false;

        if (index !== -1) {
            newHighlightedNodes.splice(index, 1);
            element.classList.remove('selected');
            element.setAttribute('aria-pressed', 'false');
        } else {
            newHighlightedNodes.push(key);
            state = true;
            element.classList.add('selected');
            element.setAttribute('aria-pressed', 'true');
        }
        this.setState(
            {
                highlightedNodes: newHighlightedNodes,
            },
            () => this.handleMouseLeave()
        );
        const selectedOption = state ? 'selected' : 'unselected';
        const payload = { field, value, state: selectedOption };
        onNodeClick({ payload });
    }

    highlightAllEdges() {
        const { highlightedNodes } = this.state;
        if (!highlightedNodes.length) {
            this.viewport
                .querySelectorAll(`[data-test^="edge-"]`)
                .forEach(element => element.classList.add('hovering'));
        }
    }

    unhighlightAllEdges() {
        const { highlightedNodes } = this.state;
        if (!highlightedNodes.length) {
            this.viewport
                .querySelectorAll(`[data-test^="edge-"]`)
                .forEach(element => element.classList.remove('hovering'));
        }
    }

    unhighlightAllNodes() {
        const { highlightedNodes } = this.state;
        if (!highlightedNodes.length) {
            this.viewport
                .querySelectorAll(`[data-test^="node-"]`)
                .forEach(element => element.classList.remove('hovering'));
        }
    }

    tween() {
        const { nodes } = this.state;
        let isAnimating = false;
        Object.keys(nodes).forEach((nodeId: string) => {
            const node = nodes[nodeId];
            const nodeElement: HTMLElement = this.viewport.querySelector(`[data-test="node-${nodeId}"]`);
            if (nodeElement) {
                if (nodeElement.style.left === '') {
                    this.moveNodeBy(
                        nodeId,
                        Math.floor(node.x),
                        Math.floor(node.y),
                        Math.floor(node.w),
                        Math.floor(node.h)
                    );
                } else {
                    const x = parseInt(nodeElement.style.left, 10);
                    const y = parseInt(nodeElement.style.top, 10);
                    const w = parseInt(nodeElement.style.width, 10);
                    const h = parseInt(nodeElement.style.height, 10);
                    node.time += 0.01;
                    const newX = Math.floor(x * (1 - node.time) + node.x * node.time);
                    const newY = Math.floor(y * (1 - node.time) + node.y * node.time);
                    const newWidth = Math.floor(w * (1 - node.time) + node.w * node.time);
                    const newHeight = Math.floor(h * (1 - node.time) + node.h * node.time);
                    if (node.time < 1) {
                        this.moveNodeBy(
                            nodeId,
                            Math.floor(newX),
                            Math.floor(newY),
                            Math.floor(newWidth),
                            Math.floor(newHeight)
                        );
                        isAnimating = true;
                    } else {
                        this.moveNodeBy(
                            nodeId,
                            Math.floor(node.x),
                            Math.floor(node.y),
                            Math.floor(node.w),
                            Math.floor(node.h)
                        );
                        node.time = 1;
                    }
                }
            }
        });
        cancelAnimationFrame(this.requestAnimationFrameId);
        if (isAnimating) {
            this.requestAnimationFrameId = requestAnimationFrame(this.tween.bind(this));
        }
    }

    moveNodeBy(nodeId: string, xIn: number, yIn: number, wIn: number, hIn: number) {
        const { nodes } = this.state;
        nodes[nodeId].currentX = xIn;
        nodes[nodeId].currentY = yIn;
        nodes[nodeId].currentW = wIn;
        nodes[nodeId].currentH = hIn;
        const nodeElement: HTMLElement = this.viewport.querySelector(`[data-test="node-${nodeId}"]`);
        if (nodeElement) {
            nodeElement.style.left = `${xIn}px`;
            nodeElement.style.top = `${yIn}px`;
            nodeElement.style.width = `${wIn}px`;
            nodeElement.style.height = `${hIn}px`;
        }
        Object.values(nodes[nodeId].data.outs).forEach((edge: EdgeType) => {
            if (edge.sourceNode === nodeId || edge.targetNode === nodeId) {
                const start = nodes[edge.sourceNode];
                const end = nodes[edge.targetNode];
                const edgeElement = this.viewport.querySelector(
                    `[data-test="edge-${edge.sourceNode}-${edge.targetNode}"]`
                );
                if (edgeElement && start && end) {
                    const middle: {
                        currentX: number;
                        currentY: number;
                        currentWidth: number;
                        currentHeight: number;
                    } = {
                        currentX: 0,
                        currentY: 0,
                        currentWidth: 0,
                        currentHeight: 0,
                    };
                    middle.currentX =
                        start.currentX +
                        start.currentW +
                        (end.currentX - (start.currentX + start.currentW)) / 2;
                    middle.currentY =
                        start.currentY +
                        start.currentH / 2 +
                        (end.currentY - start.currentY - start.currentH / 2) / 2;
                    const svgData = `
                            M${start.currentX + start.currentW},${start.currentY + start.currentH / 2}
                            L${start.currentX + start.currentW},${start.currentY + start.currentH / 2}
                            C${middle.currentX},${start.currentY + start.currentH / 2}
                            ${middle.currentX},${end.currentY + end.currentH / 2}
                            ${end.currentX},${end.currentY + end.currentH / 2}
                            L${end.currentX},${end.currentY + end.currentH / 2}`;
                    edgeElement.setAttribute('d', svgData);
                }
            }
        });
    }

    render() {
        const { nodes, edges } = this.state;
        const { nodeColor, nodeHighlightColor, linkColor, linkWidth, showValueCounts } = this.props;
        const newEdges = edges.filter((edge: EdgeType) => nodes[edge.sourceNode] && nodes[edge.targetNode]);

        return (
            <StyledViewport
                nodeColor={nodeColor}
                nodeHighlightColor={nodeHighlightColor}
                linkColor={linkColor}
                ref={c => {
                    this.viewport = c;
                }}
            >
                <Nodes
                    nodes={nodes}
                    onNodeClick={(fieldIndex: number, field: string, value: string) =>
                        this.handleMouseClick(fieldIndex, field, value)
                    }
                    handleMouseEnterNode={(nodeID: string) => this.handleMouseEnterNode(nodeID)}
                    handleMouseLeave={() => this.handleMouseLeave()}
                    showValueCounts={showValueCounts}
                />
                <Edges edges={newEdges} linkWidth={linkWidth} />
            </StyledViewport>
        );
    }
}

export default Graph;
