import * as React from 'react';
import Node from './Node';
import { NodeType } from '../PureLinkGraphUtils';

export interface NodesProps {
    nodes: Record<string, NodeType>;
    onNodeClick: (fieldIndex: number, field: string, value: string) => void;
    handleMouseEnterNode: (nodeID: string) => void;
    handleMouseLeave: () => void;
    showValueCounts: boolean;
}

const Nodes = (props: NodesProps): any =>
    Object.keys(props.nodes).map(key => (
        <button
            className="node"
            type="button"
            data-test={`node-${key}`}
            key={key}
            onClick={() =>
                props.onNodeClick(
                    props.nodes[key].data.fieldIndex,
                    props.nodes[key].data.field,
                    props.nodes[key].data.value
                )
            }
            onMouseEnter={() => props.handleMouseEnterNode(key)}
            onMouseLeave={() => props.handleMouseLeave()}
        >
            <Node data={props.nodes[key].data} showValueCounts={props.showValueCounts} />
        </button>
    ));

export default Nodes;
