import * as React from 'react';
import Paragraph from '@splunk/react-ui/Paragraph';
import { NodeDataType } from '../PureLinkGraphUtils';

export interface NodeProps {
    data: NodeDataType;
    showValueCounts: boolean;
}

const Node = (props: NodeProps): React.ReactElement => {
    const { data, showValueCounts } = props;
    return (
        <>
            <div className="center-content">
                <Paragraph>{data.value}</Paragraph>
            </div>
            <div className="right-content">
                <Paragraph>{showValueCounts ? data.count : null}</Paragraph>
            </div>
        </>
    );
};

export default Node;
