import * as React from 'react';
import Dropdown from '@splunk/react-ui/Dropdown';
import Button from '@splunk/react-ui/Button';
import Menu from '@splunk/react-ui/Menu';
import Chip from '@splunk/react-ui/Chip';

interface FieldProps {
    field: string;
    count: number;
    width: number;
    onSort: (sortKey: string) => void;
    showNodeCounts: boolean;
}

const Field = ({ field, count, onSort, showNodeCounts, width }: FieldProps): React.ReactElement => {
    const toggle = <Button label={field} isMenu appearance="toggle" />;

    return (
        <div>
            <Dropdown toggle={toggle} retainFocus data-test={`field-${field}-dropdown`}>
                <Menu style={{ width }}>
                    <Menu.Item data-test="A-Z" onClick={() => onSort('A-Z')}>
                        A-Z
                    </Menu.Item>
                    <Menu.Item data-test="Z-A" onClick={() => onSort('Z-A')}>
                        Z-A
                    </Menu.Item>
                </Menu>
            </Dropdown>
            {showNodeCounts ? (
                <Chip style={{ float: 'right', fontSize: '10px', height: '20px' }}>{count}</Chip>
            ) : null}
        </div>
    );
};

Field.displayName = 'Field';

export default Field;
